* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
.country-list__item{
  
  display: flex;
  align-items: center;
}
.country-list__svg{
  margin-right: 10px;
}
.country-list__name{
  margin: 0;
}
.country-info__wrap{
  display: flex;
  align-items: center;
}
.country-info__svg{
  margin-right: 10px;
}
.country-info__item{
  font-weight: 700;
}
.country-info__text-wrap{
  font-weight: 400;
  margin-left: 5px;
}